/* Font Stuff */
@import url("https://fonts.googleapis.com/css2?family=Chakra+Petch&display=swap");

body {
  margin: 0;
  background-color: #000;
  color: #fff;
  font-family: "Chakra Petch", sans-serif;
  font-size: 13px;
  line-height: 24px;
  overscroll-behavior: none;
}

a {
  color: #ff0;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

h1 {
  margin: 5;
  text-align: center;
  text-shadow: 0px 0px 10px#ff00ff;
  font-size: 50px;
}

h2 {
  margin: 1;
  text-align: center;
  font-size: 32px;
  text-shadow: 0px 0px 10px#ff00ff;
}

h3 {
  margin: 1;
  text-align: center;
  font-size: 22px;
  text-shadow: 0px 0px 10px#ff00ff;
}

footer {
  margin: 0;
  font-size: 32px;
  text-align: center;
}

p {
  margin: 1;
  text-align: center;
  font-size: 20px;
  padding: 0px 40px;
}

button {
  cursor: pointer;
  font-family: "Chakra Petch", sans-serif;
  font-size: 40px;
  text-shadow: 0px 0px 10px#ff00ff;
}

canvas {
  display: block;
}

#info {
  position: absolute;
  top: 0px;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  text-align: center;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  pointer-events: none;
  z-index: 1; /* TODO Solve this in HTML */
}

a,
button,
input,
select {
  pointer-events: auto;
}

.dg.ac {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  z-index: 2 !important; /* TODO Solve this in HTML */
}

#overlay {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(#000000, #612f5a);
}

#overlay button {
  background: #ac769a;
  border: 0;
  color: #000000;
  padding: 10px 20px;
  text-transform: uppercase;
  cursor: pointer;
}

#notSupported {
  width: 50%;
  margin: auto;
  background-color: #f00;
  margin-top: 20px;
  padding: 10px;
}
